* {
  box-sizing: border-box;
}

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #212121;
  background-color: #fafafa;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen-Sans, Ubuntu, Cantarell, Helvetica Neue, sans-serif;
  line-height: 1.5;
}

img {
  max-width: 100%;
  display: block;
}

html, body {
  height: 100%;
  margin: 0;
  position: relative;
}

.start-btn {
  background-color: #5d9e5d;
  border-radius: 8px;
  width: 200px;
  height: 80px;
  font-size: 20px;
  font-weight: 600;
  position: absolute;
  left: 38%;
  transform: translate(-50%);
}

.start-btn[disabled] {
  background-color: #c3c3c3;
}

.stop-btn {
  background-color: #ff5353;
  border-radius: 8px;
  width: 200px;
  height: 80px;
  font-size: 20px;
  font-weight: 600;
  position: absolute;
  left: 62%;
  transform: translate(-50%);
}

.container {
  justify-content: center;
  align-items: center;
  gap: 20px;
  display: flex;
}

#datetime-picker {
  border: 1px solid #000;
  border-radius: 4px;
  width: 200px;
  height: 50px;
}

#datetime-picker[type="text"] {
  text-align: center;
  font-family: Segoe UI, Tahoma, Geneva, Verdana, sans-serif;
  font-size: 20px;
}

#datetime-picker + button {
  text-transform: uppercase;
  border: 1px solid #000;
  border-radius: 12px;
  width: 50px;
  height: 50px;
  padding: 0;
}

#datetime-picker + button:enabled {
  transition: background-color .5s linear, box-shadow .5s linear;
  box-shadow: 0 0 10px #80cbc4;
}

#datetime-picker + button:enabled:hover {
  cursor: pointer;
  background-color: #e6e6e6;
  box-shadow: 0 0 20px 5px #80cbc4;
}

#datetime-picker + button:disabled {
  color: #840101;
  box-shadow: 0 0 10px #cb8080;
}

.hidden {
  display: none;
}

.timer {
  justify-content: center;
  column-gap: 20px;
  display: flex;
}

.field {
  flex-direction: column;
  align-items: center;
  display: flex;
}

.value {
  font-size: 50px;
  font-weight: 400;
}
